<template>
<section>
    <p>{{total}}</p>

    <table class="table table-bordered">
    <thead>
        <tr>

        </tr>
    </thead>
    <tbody>
        <tr v-for="row in rows" @click="toggleState(row.id, row.state)">
            <td :class="`star_lv${row.level}`" style="width:3em">
                <input type="checkbox" v-bind:checked="row.state=='done' ? true : false">
            </td>
            <td>{{row.item_name}}</td>
            <td>{{row.quantity}}</td>
        </tr>
    </tbody>
    </table>

    <div v-bind:class="{ 'hidden':page_end }">
        <button @click="loadNext();" class="btn btn-link" style="width:100%;">
            <p style="background-color:#cfcfcf; padding:5px; text-align:center">{{num}}</p>
        </button>
    </div>
    <div v-bind:class="{ 'hidden':!page_end }">
        <p style="background-color:#ffffef; padding:5px; text-align:center"></p>
    </div>
</section>
</template>
<script>
export default {
    data() {
        return {
            total: 0,
            rows: [],
            index: {},
            page: 1,
            page_end: false,
            paramstr: '',
        };
    },

    props: ['num', 'state', 'sort_key', 'sort_order'],

    mounted() {
        $('#loading').removeClass('hidden');
        this.loadNext();
    },

    methods: {
        loadNext() {
            $('#loading').removeClass('hidden');
            let self = this;
            let paramstr = 'num=' + self.num + '&state=' + self.state + '&sort_key=' + self.sort_key + '&sort_order=' + self.sort_order;
            let url = '/api/mustbuys?page=' + self.page + '&' + paramstr;
            axios.get(url).then(function(response) {
                self.total = response.data.meta.total;
                for (var i = 0; i < response.data.data.length; i++) {
                    let row = response.data.data[i];
                    if (typeof(self.index[row.id]) == 'undefined') {
                        self.rows.push(row);
                        self.index[row.id] = row.id;
                    } else {
                        for (var j = 0; j < self.rows.length; j++) {
                            if (self.rows[j].id == row.id) {
                                self.rows[j] = row;
                                break;
                            }
                        }
                    }
                }

                if (response.data.meta.last_page > self.page) {
                    self.page++;
                } else {
                    self.page_end = true;
                }

                $('#loading').addClass('hidden');
            });
        },

        // state
        toggleState(id, nowstate) {
            $('#loading').removeClass('hidden');
            let self = this;
            let newstate = '';
            if (nowstate == 'yet') {
                newstate = 'done';
            } else {
                newstate = 'yet';
            }
            let url = '/api/mustbuys/toggle/' + id + '/' + newstate;
            axios.put(url, {}).then(function(response) {
                console.log(response.data.data);
                for (var i = 0; i < self.rows.length; i++) {
                    if (self.rows[i].id == response.data.data.id) {
                        self.rows[i] = response.data.data;
                        break;
                    }
                }
            })
            .catch(error => {
                console.error(error);
            });
            $('#loading').addClass('hidden');
        }
    }
}
</script>
